@charset "UTF-8";
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #202020;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
}

::selection {
  background-color: orange;
  color: #fff;
}

/* font-family: "OpenSansRegular"; */
@font-face {
  font-family: "SourceSansProLight";
  src: url("../fonts/SourceSansProLight/SourceSansPro-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/SourceSansProLight/SourceSansPro-Light.woff2") format("woff2"), url("../fonts/SourceSansProLight/SourceSansPro-Light.woff") format("woff"), url("../fonts/SourceSansProLight/SourceSansPro-Light.ttf") format("truetype");
  font-style: normal;
  font-weight: 100;
  font-display: fallback;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "SourceSansProLight", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input, body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid, body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid, body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

h2,
h3,
h1 {
  font-weight: 200;
}

.block {
  min-height: 100vh;
  padding-left: 10%;
  padding-right: 10%;
}

.header_fon,
.section_wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_item {
  width: 50%;
}

.header_item img {
  display: inline-block;
  width: 70%;
  height: auto;
}

.header_content h1 {
  text-align: center;
  font-size: 2.0rem;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.header_content h3 {
  line-height: 1.9;
  margin-bottom: 50px;
}

.btn-blue {
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  color: #fff;
  text-decoration: none;
  text-transform: capitalize;
  background-color: #1089d4;
  box-shadow: 0px 8px 40px 0 rgba(0, 0, 0, 0.37);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.25);
  letter-spacing: 1px;
  border-radius: 5px 5px;
  margin: 10px 10px 0 10px 0;
  padding: 20px 30px;
  display: inline-block;
  transition: all 0.4s ease-in-out;
}

.btn-blue:hover {
  box-shadow: 0px 4px 10px 0 rgba(0, 0, 0, 0.5);
  color: #fff !important;
  background: #3498db;
}

.section-grey {
  background: #fafbfc;
}

.section-content {
  padding: 0;
  margin: 0;
}

.section-content_wrap {
  display: flex;
  justify-content: space-between;
}

.btn-blue-line {
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  color: #017df7;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid #1089d4;
  border-radius: 10px 10px;
  margin: 20px 10px 0 10px 0;
  padding: 20px 30px;
  display: inline-block;
  transition: all 0.4s ease-in-out;
}

.btn-blue-line:hover {
  color: #fff;
  background: #3498db;
}

.block_title {
  text-transform: uppercase;
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 50px;
}

.section-content_wrap_items {
  width: 30%;
}

.section-content_wrap_items picture img {
  width: 50%;
  display: block;
  height: auto;
  margin: 0 auto 20px;
}

.section-content_wrap_items h3 {
  text-align: center;
  font-size: 1.8rem;
  text-transform: uppercase;
  margin-bottom: 25px;
}

.section-content_wrap_items p {
  margin-bottom: 25px;
}

.footer_block {
  background-color: #1E90FF;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer_wrap {
  display: flex;
  justify-content: space-between;
}

.footer_item {
  width: 60%;
}

.footer_item:last-child {
  width: 40%;
}

.form_footer {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  flex-wrap: wrap;
  position: relative;
}

.form_footer .success {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  background-color: red;
  color: #fff;
  z-index: -1;
}

.form_footer .success.active {
  z-index: 0;
}

.form_footer_item {
  width: 48%;
  height: 40px;
  line-height: 40px;
  text-indent: 5px;
  margin-bottom: 20px;
  border: 1px solid #1E90FF;
  transition: all 0.5s ease;
}

.form_footer_item_s:hover,
.form_footer_item:hover {
  border: 1px solid #0077ea;
}

.form_footer_item_s {
  width: 100%;
  margin-bottom: 20px;
}

.form_footer_text {
  padding-top: 5px;
  min-height: 200px;
  width: 100%;
  text-indent: 5px;
  border: 1px solid #1E90FF;
  transition: all 0.52s ease;
  resize: none;
}

.error_box {
  width: 100%;
  height: 50px;
  position: relative;
}

.error_message {
  position: absolute;
  left: 0;
  padding-left: 10px;
  top: 0;
  color: #fff;
  width: 100%;
  margin-bottom: 10px;
  font-size: 1.28rem;
  min-height: 30px;
}

.form_footer_text:hover {
  border: 1px solid #0077ea;
}

.form_footer_submit {
  margin: auto;
  padding: 15px 30px;
  color: #fff;
  color: #1E90FF;
  text-transform: uppercase;
  font-weight: 900;
  border: none;
  cursor: pointer;
}

.checkbox_box {
  margin: 10px auto;
  width: 100%;
  padding: 0;
  color: #fff;
  line-height: 25px;
}

.ans-label {
  color: #4a4b4b;
  font-weight: 600;
  padding-top: 7px;
}

.ans-radio {
  color: #4a4b4b;
  font-weight: normal;
  padding-right: 10px;
  vertical-align: middle;
}

.ans-radio {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1em;
  padding: 0;
  font-size: 13px;
}

.ans-label {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  /*margin-bottom: 1em;*/
  padding: 0;
  font-size: 13px;
}

.checkbox_box .ans-label input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0.7em;
  margin-left: 0;
}

.checkbox_box .ans-label input[type="checkbox"] {
  position: relative;
  width: 21px;
  height: 20px;
  border: 1px solid #515151;
  border-radius: 2px;
  background-color: #1E90FF;
  margin-bottom: 1em;
}

.checkbox_box .ans-label input[type="checkbox"]::after {
  content: '';
  width: 70%;
  height: 45%;
  border-radius: 0;
  margin: auto;
  position: absolute;
  top: 3px;
  left: 3px;
  -webkit-transition: background 0.25s ease;
  transition: background 0.25s ease;
}

.checkbox_box .ans-label input[type="checkbox"]:checked {
  background-color: #515151;
}

.checkbox_box .ans-label input[type="checkbox"]:checked::after {
  border-left: 3px solid;
  border-bottom: 3px solid;
  border-color: #1E90FF;
  border-radius: 0;
  transform: rotate(-45deg) translate3d(0, 0, 0);
  transform-origin: center center;
}

/* IE 10+ */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .checkbox_box .ans-label input[type="checkbox"] {
    border: none;
  }
  .checkbox_box .ans-label input[type="checkbox"] {
    background-color: transparent;
  }
}

